import en from './en'

function typeGuard<T extends Record<keyof typeof en, string>>(object: T) {
    return object
}

const pl = typeGuard(
    Object.freeze({
        LogIn: 'Zaloguj si',

        Password: 'haso',
        Username: 'nazwa uytkownika',

        CreateAnAccount: 'stwrz nowe konto',
        Register: 'zarejestruj si',
        RepeatPassword: 'powtrz haso',
    })
)

export default pl
